import React from 'react';
import styles from './List.module.scss'
import { getArticleList } from '../../api/ArticlesAPI';
import { IArticle } from '../../types/index';
import ListItem from './ListItem';
import { Select } from 'antd';
import { placeArr, useEventListener } from '../../utils';
import AutoResponsive from "autoresponsive-react";

export default function List() {
  const getCurClientWidth = function () {
    return Math.floor(document.body.clientWidth * 0.96)
  }
  const [list, setList] = React.useState<Array<IArticle>>([]);
  const [clientWidth, setClientWidth] = React.useState(getCurClientWidth());
  const autoResponsiveProps = {
    itemMargin: 10,
    itemClassName: 'articleItem',
    transitionDuration: '.5',
    transitionTimingFunction: 'easeIn',
    containerWidth: clientWidth,
  };
  const resize = function () {
    setClientWidth(getCurClientWidth);
  }
  useEventListener('resize', resize);
  React.useEffect(() => {
    getArticleList().then((data: any) => {
      setList(data);
    });
  }, []);
  const { Option } = Select;
  const onSearch = () => {
    debugger
  }
  function onPlaceChange(val: any) {
    debugger
  }
  return (
    <div id="home-list" className={styles.homeList}>
      <div className={styles.searchContainer}>
        {/* <Select showSearch onChange={onPlaceChange} style={{ width: 200 }} placeholder="选择地点关键词">
          {
            placeArr.map((val, index) => {
              return <Option key={index} value={val}>{val}</Option>
            })
          }
        </Select> */}
      </div>
      <div className={styles.listContainer}>
        {<AutoResponsive   {...autoResponsiveProps} >
          {
            list.map((item) => {
              const itemWidth = Math.floor((clientWidth / 2 - 10));
              const titleHeight = 48, contentHeight = item.frontContent ? 54 : 0, authorHeight = 24, dataHeight = 54, tagHeight = (Math.ceil(item.keyWords.length / 3)) * 22;
              const bottomHeight = titleHeight + contentHeight + authorHeight + dataHeight + tagHeight + 92;//50 margin
              const itemHeight = item.imgRate ? (itemWidth * item.imgRate + bottomHeight) : bottomHeight;
              return <ListItem key={item.link} article={item} style={{ width: itemWidth, height: itemHeight }} />
            })
          }
        </AutoResponsive>}
      </div>
    </div>
  );
}
